
.footer-info{
  margin-bottom: 1.375rem;
  display: flex;
  justify-content: space-between;
  align-content: center;
  .copyright{
    text-align: center;
    font-size: 0.75rem;
    line-height: 0.75rem;
  }
  .media{
    display: flex;
    justify-content: flex-end;
    .media__fb{
      width: 1rem;
      height: 1rem;
      padding-right: 2rem;
    }
   .media__insta{
      width: 1rem;
      height: 1rem;
    }
  }
}

@media (min-width: $breakpoint-sm) {
  .footer-info{
    .copyright{
      font-size: 1.125rem;
      line-height: 1.5rem;
    }
    .media{
      .media__fb{
        width: 1.875rem;
        height: 1.875rem;
        padding-right: 1.25rem;
      }
      .media__insta{
        width: 1.875rem;
        height: 1.875rem;
      }
    }
  }
}

@media (min-width: $breakpoint-lg) {
  .footer-info {
    margin-right: 8.875rem;
  }
}


